import { useEffect, useState, useRef, Dimensions, Image, StyleSheet, View  } from 'react';
import { motion, useAnimation, useMotionValue, useTransform } from "framer-motion";
import { useInView } from "react-intersection-observer" 
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import AOS from "aos";
import "aos/dist/aos.css"
import {fadeIn} from "react-fade-in"
import "./style.css"
import {useSpring, animated as a} from "react-spring"
import {Carousel} from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import Swiper from 'swiper';
import { useMediaQuery } from 'react-responsive';
import "../node_modules/swiper/swiper.css";
import "../node_modules/swiper/swiper.min.css";

import swipeImage from "./Images/swipe.png"










function Home() {


  // hamburguer
  const [active, setActive] = useState(false); 
  const toggleMenu = () => { 
    setActive(!active);
  };

// Carousel Primeira Tentativa


 // carousel e pegando coisas do service.json

 const [data, setData] = useState([]);
 const isMobile = window.innerWidth <= 1000; // para definir o tamanho da tela pro swiper
  const carousel = useRef();
  const [currentPage, setCurrentPage] = useState(0);
  const [width, setWidth] = useState(0); // limitar a passada máxima do swiper
  

  useEffect(() => {
    fetch("http://localhost:3000/static/services.json")
      .then((response) => response.json())
      .then(setData);
      //setWidth(carousel.current.scrollWidth - carousel. current.offsetWidth)
    const interval = setInterval(() => {
      if (data && data.length > 0) {
        setCurrentPage((currentPage + 1) % data.length);
      }
    }, []);

    // swiper

    
  
    return () => clearInterval(interval);
  }, [currentPage, data]);
  
  // useEffect foi usado para realizar a chamada da API, que é executado uma vez devido ao array vazio, usei o método fetch para fazer a chamada e a resposta é trannsformada em um objeto JSON com o método ".json()"


  const handleLeftClick= (e) =>{
    e.preventDefault();
 
  // Move o carrossel
  
   setTimeout(()=>{
     carousel.current.scrollLeft -= carousel.current.offsetWidth;
 
   }, 200)
   
   
  
  }
  
    const handleRightClick = (e) =>{
    e.preventDefault();
   
    // Move o carrossel
    
    setTimeout(()=>{
      carousel.current.scrollLeft += carousel.current.offsetWidth;
      
    }, 200)
    


   
  
  };


// Cursor effect
  const [mousePosition, setMousePosition] = useState({
    x: 0,
    y: 0
  })
  const [cursorVariant, setCursorVariant] = useState("default");
  
  useEffect(() =>{
    const mouseMove = e => {
      setMousePosition({
        x: e.clientX,
        y: e.clientY
      })
      
    }
    

    window.addEventListener("mousemove", mouseMove)

    
    return () => {
      window.removeEventListener("mousemove", mouseMove)
    }
  }, []); // executado uma vez devido à array vazia
  
  const variants = {
    default: {
      x: mousePosition.x - 16,
      y: mousePosition.y - 16  
    },
    container: {
      
      backgroundColor: "orange",
      x: mousePosition.x - 16,
      y: mousePosition.y - 16
    },

    
  }

  const containerEnter = () => setCursorVariant("container");
  const containerLeave = () => setCursorVariant("default")



  

    // scroll effect


    useEffect(()=>{
      AOS.init({
        duration: 1000, // duração da animação em milissegundos
        offset: 200, // offset (em pixels) em relação ao topo do elemento em que a animação é disparada
        easing: "ease-in-out", // tipo de easing utilizado na animação
        delay: 100 // tempo (em milissegundos) de atraso antes do início da animação
      });
      
     
    }, [])
  

  // Swiper

 if (!data || !data.length) return null;
  
  return (
    <>
    <navBar 
    onMouseEnter={containerEnter} 
    onMouseLeave={containerLeave}
    >
    <motion.div 
    className="cursor"
    variants={variants}
    animate={cursorVariant}
    />
    <motion.div 
    className='cursor2'
    variants={variants}
    animate={cursorVariant}
    />
      <div className={active ? "icon iconActive" : "icon"} onClick={toggleMenu}>
        <div className="hamburguer hamburguerIcon"></div>
      </div>
      <div className={active ? "menu menuOpen" : "menu menuClose"}>

        <div className="list">
          <ul className="listItems">
            <li><a href="navBar">HOME</a></li>
            <li><a href="#">WORK</a></li>
            <li><a href="#">ABOUT</a></li>
            <li><a href="#">CONTACT</a></li>
        </ul>
        <h4>Vamos encontrar soluções juntos?</h4>
        <a className="EntrarContato">Entre em contato para construirmos o seu projeto juntos!</a>
        <a className="email">erickrodovalhosilveira@gmail.com</a>
    
        </div>
      </div>
      <div className="logo">
        <a className>Ers</a>
</div>
<div className="container"
 data-aos="fade-up"
 data-aos-duration="1000"
 data-aos-once="true"
 data-aos-offset="0"
>
<motion.div className="carousel" ref={carousel}
 whileTap={isMobile ? { cursor: "grabbing" } : null}
 drag={isMobile ? "x" : null}
 dragConstraints={{right: 0, left: -1000}}
>
        {data.map((item, index) => {
         const { id, title, subtitle, description, photo } = item;
         const isActive = index === currentPage;
          return (
            
            <motion.div
             className={`item ${isActive ? 'active' : ''}`} key={id}
             

             >
           
              <div className="photo photoExcluir">
              <img src={photo} alt={title} />
              </div>
              <motion.div className="info" 
              
              
              >
             
                <span className="title">{title}</span>
                <span className="subtitle">{subtitle}</span>
                <span className="description ">{description}</span>

                
              </motion.div>
            
            
                
            </motion.div>
           
            
          );
        })}
  


      </motion.div>
      
      <div className="buttons">
      <button onClick={handleLeftClick} 
      className='esquerda'>
          <img src="/static/images/216151_right_chevron_icon.png" alt="Scroll Left" />
        </button>
        <button onClick={handleRightClick} 
        className='direita'>
          <img src="/static/images/216151_right_chevron_icon.png" alt="Scroll Right" />
        </button>
      </div>
      </div>
    

</navBar>



<aboutMe className="aboutBox">

<div className='about box' 
  data-aos="fade-up"
 data-aos-duration="1500"
  data-aos-once="true"
  data-aos-offset="0"
  >
<p>Olá! Eu sou um desenvolvedor web fullstack de Anápolis, GO. Meu foco é em desenvolvimento MERN (MongoDB, Express, Reactjs, Nodejs).</p>
<button className='aboutButton'><a href='#'>Mais sobre mim</a></button>
</div>


</aboutMe>

<myServices
 data-aos="fade-up"
 data-aos-duration="2000"
 data-aos-once="true"
 data-aos-offset="0"
>
 
 <h1 >
  Um pouco dos meus projetos:
  </h1>
 
 <h2 className='project1'>Projeto 1</h2>
 <p className='explanation1'>Explicação 1</p>
 <h2 className='project2'>Projeto 2</h2>
 <p className='explanation2'>Explicação 2</p>
 <h2 className='project3'>Projeto 3</h2>
 <p className='explanation3'>Explicação 3</p>
 <h2 className='project4'>Projeto 4</h2>
 <p className='explanation4'>Explicação 4</p>

 
</myServices>

<workMe>
  <h1 className='letsCode'>
    Vamos programar algo <span className='color1'>juntos?</span>
  </h1>
  <button className='contactButton'>
    <a href='#'>Contato</a>
  </button>
  <h1 className='socialMedia'>
    Você pode me achar nas <span className='color2'>redes sociais:</span>
  </h1>
  <a href='#' className='linkedin'>Linkedin</a>
  <a href='#' className='github'>GitHub</a>
  <a href='#' className='instagram'>Instagram</a>
  <a href='#'className='whatsapp'>WhatsApp</a>

  <h4>Copyright © Erick Rodovalho da Silveira. All rights reserved</h4>
</workMe>

    </>
  );
}





function AboutMe(){
//hamburguer
const [active, setActive] = useState(false);
const toggleMenu = () => {
  setActive(!active);
};
// Cursor effect
const [mousePosition, setMousePosition] = useState({
  x:0,
  y:0
})
const [cursorVariant, setCursorVariant] = useState("default");
  
  useEffect(() =>{
    const mouseMove = e => {
      setMousePosition({
        x: e.clientX,
        y: e.clientY
      })
      
    }
    

    window.addEventListener("mousemove", mouseMove)

    
    return () => {
      window.removeEventListener("mousemove", mouseMove)
    }
  }, []); // executado uma vez devido à array vazia
  
  const variants = {
    default: {
      x: mousePosition.x - 16,
      y: mousePosition.y - 16  
    },
    container: {
      x: mousePosition.x - 16,
      y: mousePosition.y - 16
    },

}

const containerEnter = () => setCursorVariant("container");
const containerLeave = () => setCursorVariant("default")


// scroll effect

useEffect(()=>{
AOS.init({
  duration: 1000,
  offset: 200,
  easing: "ease-in-out",
  delay: 100
});

},[])

return(
<Router>
<Routes>
  <Route exact path="/" component={Home}/>
  <Route exact path="/AboutMe" component={AboutMe}/>
</Routes>
<navBar2
onMouseEnter={containerEnter}
onMouseLeave={containerLeave}
>
  <motion.div
  className="cursor"
  variants={variants}
  animate={cursorVariant}
  />
  <motion.div 
    className='cursor2'
    variants={variants}
    animate={cursorVariant}
    />

</navBar2>
</Router>
)
}

export default {AboutMe, Home};